.select2-container--classic {
	.select2-search--dropdown {
		.select2-search__field {
			border: 1px solid @select2-border-color;
			outline: 0;
		}
	}
	.select2-search--inline {
		.select2-search__field {
			outline: 0;
		}
	}
	.select2-dropdown {
		background-color: @select2-dropdown-bg-color;
		border: 1px solid transparent;
	}
	.select2-dropdown--above {
		border-bottom: none;
	}
	.select2-dropdown--below {
		border-top: none;
	}
	.select2-results {
		&  > .select2-results__options {
			max-height: @select2-results-max-height;
			overflow-y: auto;
		}
	}
	.select2-results__option {
		&[role=group] {
			padding: 0;
		}
		&[aria-disabled=true] {
			color: @select2-results-choice-fg-unselectable-color;
		}
	}
	.select2-results__option--highlighted[aria-selected] {
		transition: background @animation-time;
		background-color: @select2-results-choice-bg-hover-color;
		color: @select2-results-choice-fg-hover-color;
	}
	.select2-results__group {
		cursor: default;
		display: block;
		// padding: 6px;
	}
	.select2-results__options--nested {
		.select2-results__option {
			padding: 10px 40px;
		}
	}
	&.select2-container--open .select2-dropdown {
		border-color: @select2-focus-border-color;
	}
}
